.group__face,
.group__hair,
.group__nose,
.group__ear-right,
.group__ear-left,
.group__neck,
.group__body,
.profile__group,
.group__fringe,
.group__eye,
.group__tongue,
.group__mouth,
.group__eyebrown,
.group__blush {
  position: absolute;
}

.profile-picture {
  transform: scale(1.6);
  width: 200px;
  height: 200px;
  box-sizing: border-box;
  border-radius: 200px;
  overflow: hidden;
  background: #e8e9eb;
  border: 3px solid black;
  transition: all ease 3.2s;

  .profile__group {
    left: -12px;
  }
}

.group__face {
  border-radius: 100px;
  z-index: 20;
  top: 50px;
  left: 70px;
  width: 80px;
  height: 100px;
  background: #f0beaf;
}
